<template>
  <div class="header">
    <div class="bottom">
      <div class="warp">
        <div class="Icimg"><img src="@/assets/img/end.png" alt=""/></div>
        <p>提交完成</p>
        <span>您的邮箱将会收到会议信息，请查收</span>
      </div>
      <div class="warp">
        <div class="warpTop">
          <div class="form-warp">
            <span>主持人链接:</span>
            <input class="form-input" v-model="List.host" type='text' ref="copyA" readonly/>
            <i class="iconfont icon-link" @click="copyA"></i>
          </div>
          <div class="form-warp">
            <span>加入者链接:</span>
            <input class="form-input" v-model="List.addUrl" type='text' ref="copyB" readonly/>
            <i class="iconfont icon-link" @click="copyB"></i>
          </div>
          <div class="form-warp">
            <span>CMR地址:</span>
            <input class="form-input" v-model="List.CMR" type='text' ref="copyC" readonly/>
            <i class="iconfont icon-link" @click="copyC"></i>
          </div>
          <div class="form-warp">
            <span>直播地址:</span>
            <input class="form-input" v-model="List.Live" type='text' ref="copyD" readonly/>
            <i class="iconfont icon-link" @click="copyD"></i>
          </div>
          <div class="form-warp">
            <span>开始时间:</span>
            <input class="form-input" v-model="List.starttime" type='text' readonly/>
          </div>
        </div>
        <hr/>
        <div class="warpBottom">
          <div class="form-warp">
            <span>会议名称:</span>
            <input class="form-input" v-model="List.meetingname" type='text' readonly/>
          </div>
          <div class="form-warp">
            <span>会议号:</span>
            <input class="form-input" v-model="List.Number" type='text' readonly/>
          </div>
          <div class="form-warp">
            <span>会议密码:</span>
            <input class="form-input" v-model="List.Meeting" type='text' readonly/>
          </div>
          <div class="form-warp">
            <span>主持人密钥:</span>
            <input class="form-input" v-model="List.Hostpaaword" type='text' readonly/>
          </div>
        </div>
      </div>
    </div>
    <router-link to="List">
      <button class="btnA" @click="EndRouter">返回</button>
    </router-link>
  </div>
</template>
<script>
import axios from 'axios'
import Cookies from 'js-cookie'
export default{
  name: 'EndDetails',
  data () {
    return {
      List: {
        host: '',
        addUrl: '',
        CMR: '',
        Live: '',
        Number: '',
        Meeting: '',
        Hostpaaword: '',
        starttime: '',
        meetingname: ''
      },
      formList: []
    }
  },
  methods: {
    getList () {
      const token = Cookies.get('name')
      let date = new Date()
      let Y = date.getFullYear() + '-'
      let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
      let D = date.getDate() + ' '
      let h = date.getHours() + ':'
      let m = date.getMinutes() + ':'
      let s = date.getSeconds()
      const NewTime = Y + M + D + h + m + s
      axios({
        method: 'post',
        url: '/api/5b4d716111a59?t=' + NewTime,
        headers: {'version': 'v3.0', 'access-token': token},
        data: {
          meetingKey: this.$route.query.id
        },
        responseType: 'json'
      }).then(this.HandleFor)
    },
    copyA () {
      this.$refs.copyA.select()
      document.execCommand('copy')
      this.$layer.msg('已复制链接')
    },
    copyB () {
      this.$refs.copyB.select()
      document.execCommand('copy')
      this.$layer.msg('已复制链接')
    },
    copyC () {
      this.$refs.copyC.select()
      document.execCommand('copy')
      this.$layer.msg('已复制链接')
    },
    copyD () {
      this.$refs.copyD.select()
      document.execCommand('copy')
      this.$layer.msg('已复制链接')
    },
    HandleFor (res) {
      const data = res.data
      if (data.code === -996) {
        alert(this.PublicNum.ber)
        if (this.PublicNum.ber <= 3) {
          this.PublicNum.setname()
          this.funcName()
          //  const token = Cookies.get('name')
          this.getList()
          //  location.reload()
          this.$layer.msg(res.data.msg)
        } else {
          this.$layer.msg('网络链接错误')
        }
      } else {
        this.PublicNum.endnumber()
        if (res.data.code === 1) {
          this.formList = res.data.data
          this.List.host = this.formList.hostjoinurl
          this.List.addUrl = this.formList.guestjoinurl
          this.List.CMR = this.formList.meetingname
          this.List.Live = this.formList.meetingname
          this.List.Number = this.formList.meetingkey
          this.List.Meeting = this.formList.meetingpassword
          this.List.Hostpaaword = this.formList.hostpassword
          this.List.starttime = this.formList.starttime
          this.List.meetingname = this.formList.meetingname
          //  location.reload()
          return true
        } else if (res.data.code === -7) {
          this.$layer.msg('请从新登陆')
          return false
        }
      }
    },
    EndRouter () {
      this.$router.push('List')
      location.reload()
    }
  },
  mounted () {
    this.getList()
  }
}
</script>
<style lang="stylus" scoped>
  .header
    margin-bottom 1.5rem
  @import "~style/input.styl"
  @import "~style/var.styl"
  .warp
    width 92%
    margin .5rem auto
    border-radius .1rem
    text-align center
    padding-top .4rem
    box-shadow 0 0 15px #e5e5e5
    .Icimg
      width 1.4rem
      height 1.4rem
      margin auto
      img
        width 100%
        height 100%
    p
      margin .4rem 0
      color #333
      font-size .38rem
    span
      display block
      color #666
      padding-bottom .5rem
    i
      color #1C9CF5
    .warpTop,.warpBottom
      .form-warp
        display flex
        height $Height
        padding 0 .2rem
        line-height $Height
        .form-input
          flex 1
          padding 0 .1rem
          font-size .3rem
          text-indent .18rem
          color #333
        span
          width 1.68rem
          text-align left
          font-size .3rem
          color #666
          overflow hidden
  hr
    margin 0 .2rem
  .warpBottom
    padding-bottom .5rem
  .btnA
    Btn()
</style>



